/* eslint-disable camelcase */
import React, { useEffect, useState } from 'react';
import {
  Button,
  Card,
  CardBody,
  FormGroup,
  Input,
  Label,
  Row,
} from 'reactstrap';
import { useHistory } from 'react-router-dom';
import { connect } from 'react-redux';

import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import IntlMessages from '../../../helpers/IntlMessages';

import { adminRoot } from '../../../constants/defaultValues';
import {
  getAllHackathons,
  updateCurrentHackathon,
} from '../../../redux/actions';
import { NotificationManager } from '../../../components/common/react-notifications';
import useRequest from '../../../hooks/use-request';
import { wait } from '../../../helpers/Utils';

const Links = ({
  match,
  accessToken,
  updateAction,
  currentHackathon,
  refresh,
  loading,
}) => {
  const { id } = match.params;

  const history = useHistory();

  if (!id) {
    history.goBack();
  }

  useEffect(() => {
    updateAction(id);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const {
    hackathon_website,
    portfolio_url,
    contact_email,
    link_to_code_of_control,
    facebook,
    twitter,
    instagram,
    linkedin,
  } = currentHackathon;
  console.log(currentHackathon);
  const [values, setValues] = useState(()=>({
    hackathon_website,
    portfolio_url,
    contact_email,
    link_to_code_of_control,
    facebook,
    twitter,
    instagram,
    linkedin,
  }));

  useEffect(() => {
    setValues({
      ...values,
      hackathon_website: currentHackathon.hackathon_website,
      portfolio_url: currentHackathon.portfolio_url,
      contact_email: currentHackathon.contact_email,
      link_to_code_of_control: currentHackathon.link_to_code_of_control,
      facebook: currentHackathon.facebook,
      twitter: currentHackathon.twitter,
      instagram: currentHackathon.instagram,
      linkedin: currentHackathon.linkedin,
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentHackathon]);

  const handleChange = (event, key) => {
    setValues({
      ...values,
      [key]: event.target.value,
    });
  };

  const [doRequest, requestLoading] = useRequest({
    url: '/updateHackathon',
    method: 'POST',
    headers: {
      Authorization: accessToken,
    },
    data: { id, ...values },
    nextSuccess: () => {
      NotificationManager.success(
        'Hackathon updated successfully!',
        'Hackathon Operation Success',
        3000,
        null,
        null,
        ''
      );
      refresh(accessToken, id);
      wait(1200, () => {
        history.push(`${adminRoot}/challenges/brands/${id}`);
      });
    },
    nextError: (error) => {
      NotificationManager.error(
        error,
        'Hackathon Operation Error',
        3000,
        null,
        null,
        ''
      );
    },
  });

  const handleSubmit = () => {
    console.log(values);
    if (
      document.getElementById("web").value === '' ||
      document.getElementById("em").value === '' ||
      document.getElementById("loc").value === '' ||
      document.getElementById("tweet").value === '' ||
      document.getElementById("insta").value === '' ||
      document.getElementById("fb").value === '' ||
      document.getElementById("linked").value === ''
    ) {
      NotificationManager.error(
        'Please enter all links!',
        'Hackathon Operation Error',
        3000,
        null,
        null,
        ''
      );
    } else {
      doRequest();
    }
  };

  if (loading) return <div className="loading" />;
console.log(values);
  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.links" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>

      <Row className="justify-content-center">
        <Colxx xxs="12" md="10" className="mb-4">
          <Card className="mb-4">
            <CardBody>
              <FormGroup className="mb-6">
                <Label>
                  <IntlMessages id="challenges.hackathon-website" />
                </Label>
                <Input
                id="web"
                  type="text"
                  className="form-control"
                  name="hackathon-website"
                  value={values.hackathon_website}
                  onChange={(event) => {
                    handleChange(event, 'hackathon_website');
                  }}
                  placeholder='Enter Hackathon Website'
                />
              </FormGroup>

              {/* <FormGroup className="mb-6">
                <Label>
                  <IntlMessages id="challenges.portfolio-url" />
                </Label>
                <Input
                  type="text"
                  className="form-control"
                  name="potfolio-website"
                  value={values.portfolio_url || ''}
                  onChange={(event) => {
                    handleChange(event, 'portfolio_url');
                  }}
                />
              </FormGroup> */}
            </CardBody>
          </Card>

          <Card className="mb-4">
            <CardBody>
              <FormGroup>
                <Label>
                  <IntlMessages id="challenges.contact-email" />
                </Label>
                <Input
                id="em"
                  type="text"
                  className="form-control"
                  name="contact-email"
                  value={values.contact_email}
                  onChange={(event) => {
                    handleChange(event, 'contact_email');
                  }}
                />
              </FormGroup>

              <FormGroup>
                <Label>
                  <IntlMessages id="Code of conduct" />
                </Label>
                <Input
                id="loc"
                  type="text"
                  className="form-control"
                  name="link-to-code-of-control"
                  value={values.link_to_code_of_control}
                  onChange={(event) => {
                    handleChange(event, 'link_to_code_of_control');
                  }}
                />
              </FormGroup>
            </CardBody>
          </Card>

          <Card className="mb-4">
            <CardBody>
              <FormGroup className="mb-6">
                <Label>
                  <IntlMessages id="challenges.facebook" />
                </Label>
                <Input
                id="fb"
                  type="text"
                  className="form-control"
                  name="facebook"
                  value={values.facebook}
                  onChange={(event) => {
                    handleChange(event, 'facebook');
                  }}
                />
              </FormGroup>

              <FormGroup className="mb-6">
                <Label>
                  <IntlMessages id="challenges.twitter" />
                </Label>
                <Input
                id="tweet"
                  type="text"
                  className="form-control"
                  name="twitter"
                  value={values.twitter}
                  onChange={(event) => {
                    handleChange(event, 'twitter');
                  }}
                />
              </FormGroup>

              <FormGroup className="mb-6">
                <Label>
                  <IntlMessages id="challenges.linkedin" />
                </Label>
                <Input
                id="linked"
                  type="text"
                  className="form-control"
                  name="linkedin"
                  value={values.linkedin}
                  onChange={(event) => {
                    handleChange(event, 'linkedin');
                  }}
                />
              </FormGroup>

              <FormGroup className="mb-6">
                <Label>
                  <IntlMessages id="challenges.instagram" />
                </Label>
                <Input
                id="insta"
                  type="text"
                  className="form-control"
                  name="instagram"
                  value={values.instagram}
                  onChange={(event) => {
                    handleChange(event, 'instagram');
                  }}
                />
              </FormGroup>
            </CardBody>
          </Card>

          <FormGroup className="text-center">
            <Button
              id="submit-links"
              type="submit"
              color="primary"
              className={`btn-shadow btn-multiple-state ${
                requestLoading ? 'show-spinner' : ''
              }`}
              onClick={handleSubmit}
            >
              <span className="spinner d-inline-block">
                <span className="bounce1" />
                <span className="bounce2" />
                <span className="bounce3" />
              </span>
              <span className="label">
                <IntlMessages id="forms.update" />
              </span>
            </Button>
          </FormGroup>
        </Colxx>
      </Row>

      {/* <Row>
        <Colxx>
          <FormGroup className="text-center">
            <Button
              color="primary"
              className="m-3"
              onClick={() =>
                history.push(`${adminRoot}/challenges/basics/${id}`)
              }
            >
              <IntlMessages id="forms.previous" />
            </Button>
          </FormGroup>
        </Colxx>
      </Row> */}
    </>
  );
};

const mapStateToProps = ({ authUser, hackathonData }) => {
  const { accessToken } = authUser;
  const { currentHackathon, loading, error } = hackathonData;
  return {
    accessToken,
    currentHackathon,
    loading,
    error,
  };
};

export default connect(mapStateToProps, {
  updateAction: updateCurrentHackathon,
  refresh: getAllHackathons,
})(Links);
